import { Alert, Box, Button, CircularProgress, Stack, Typography } from "@mui/material";
import axios from "axios";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";

import { ClientJS } from 'clientjs';

import Promo from "./Interfaces/Promo";
import Voucher from "./Interfaces/Voucher";


const PromoPage = () => {
  const { promoId } = useParams();

  const [promo, setPromo] = useState<Promo | null>(null);
  const [voucher, setVoucher] = useState<Voucher | null>(null);

  const [errorMessage, setErrorMessage] = useState<string | null>(null);
  const [state, setState] = useState<'promo' | 'voucher'>('promo');

  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);

      const promo = await fetchPromo();
      setPromo(promo);

      setLoading(false);

      if (!promo) return;

      if (promo.remaining <= 0) {
        setErrorMessage('Promo ini sudah habis diklaim.');
      }
      if (new Date(promo.expiry) < new Date()) {
        setErrorMessage('Promo ini sudah berakhir.');
      }  
    }

    fetchData();
  }, []);

  const fetchPromo = async () => {
    try {
      const ret = await axios.get<Promo>(`/api/promo/${promoId}`);
      const promo = ret.data;

      return promo;
    } catch (error) {
      if (axios.isAxiosError(error)) {
        const data = error.response?.data;

        setErrorMessage(data?.error ?? '');
      } else {
        setErrorMessage((error as any)?.message ?? '');
      }

      return null;
    }
  }

  const fetchVoucher = async (promo: Promo) => {
    if (!isPromoValid(promo)) {
      alert('not valid!');
      return null;
    }

    const client = new ClientJS();
    const fingerprint = client.getFingerprint();

    const ret = await axios.post<Voucher>(`/api/promo/${promoId}/voucher`, { user: `${fingerprint}` });

    return ret.data;
  }

  const handleRedeem = async () => {
    setLoading(true);

    const voucher = await fetchVoucher(promo!);

    if (voucher) {
      console.log('Voucher', voucher);
      setVoucher(voucher);
      setState('voucher');
    } else {

    }

    setLoading(false);
  }

  const isPromoValid = (promo: Promo) => {
    if (promo.remaining <= 0) return false;
    if (new Date(promo.expiry) < new Date()) return false;

    return true;
  }

  const formatDate = (date: Date | string) => {
    return new Date(date).toLocaleDateString('id', { year: 'numeric', month: 'long', day: 'numeric' });
  }

  if (loading) {
    return (
      <CircularProgress />
    );
  }

  if (state === 'promo') {
    return (
      <Stack direction='column' sx={{ width: '100%' }}>
        {errorMessage &&
          <Alert severity="error">{errorMessage}</Alert>
        }
        {promo &&
          <>
            <img src={`${promo.image}`} width='80%' style={{ alignSelf: 'center', marginTop: '20px' }} />
            <Typography fontWeight='bold' alignSelf='center' marginTop='20px' fontSize='18pt'>{promo.name}</Typography>
            <Typography alignSelf='center' marginTop='10px' fontSize='12pt'>{promo.description}</Typography>

            <Typography alignSelf='center' marginTop='20px'>Berlaku sampai {formatDate(promo.expiry)}.</Typography>

            {!errorMessage &&
              <Button type='submit' sx={{
                backgroundColor: 'black',
                color: 'white',
                mx: '20px', mt: '20px', mb: '20px', p: '15px', textTransform: 'none', fontSize: '20pt',
                borderRadius: '20px',
              }}
                variant="contained"
                onClick={handleRedeem}
              >Klaim</Button>
            }
          </>
        }
      </Stack>
    );
  }

  else if (state === 'voucher' && voucher) {
    return (
      <Stack direction='column' sx={{ width: '100%', height: '100dvh', alignItems: 'center' }}>
        <Box flexGrow='1' />

        <Typography marginTop='10px' fontSize='18pt'>Voucher anda:</Typography>
        <Typography fontWeight='bold' marginTop='10px' fontSize='40pt'>{voucher.code.toUpperCase()}</Typography>

        <Box flexGrow='1' />

        {voucher.claimed ?
          <Alert severity="error" sx={{ width: '100%', '& .MuiAlert-icon': { fontSize: '30pt' }, fontSize: '15pt', alignItems: 'center' }}>
            Voucher ini sudah pernah diklaim pada {formatDate(voucher.claimedAt)}
          </Alert>
          :
          <Alert severity="success" sx={{ width: '100%', '& .MuiAlert-icon': { fontSize: '30pt' }, fontSize: '15pt', alignItems: 'center' }}>
            Tunjukkan kode ini ke kasir.
          </Alert>
        }
      </Stack>
    );
  }

  return <div></div>;
}

export default PromoPage;